import http from '@/components/utils/http.js';
export default {
	data(){
		return{
			noname:"",
			bankname:"",
			bankno:"",
			
		}
	},
	methods:{
		navTo(url) {
			uni.navigateTo({
				url
			});
		},
		
		nonameInpur(e) {
			this.noname = e.detail.value
		},
		banknameInpur(e) {
			this.bankname = e.detail.value
		},
		banknoInpur(e) {
			this.bankno = e.detail.value
		},
		
		savebank(){
			var _that = this;
			if (_that.noname == '') {
				this.$api.msg('银行账户名不能为空');
				return;
			} else if (_that.bankname =='') {
				this.$api.msg('银行名不能为空');
				return;
			} else if (_that.bankno == '') {
				this.$api.msg('卡号不能为空');
				return;
			} else {
				const data = {
					noname: _that.noname,
					bankname: _that.bankname,
					bankno: _that.bankno,
				};
				this._submitrequest(data);
			}
			
			
		},
		
		_submitrequest(data) {
			let _this=this;
			let opts = {
				url: '/base/savebank/',
				method: 'post'
			};
			let param = data;
			http.httpTokenRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					if (res.data['code'] == 0) {
						this.$api.msg("修改成功");
					} else {
						this.$api.msg(res.data.msg);
					}
				},
				error => {
					console.log(error);
				}
			);
		},
		
		_loadbank() {
			uni.showLoading({
				title:"加载中.."
			})
			let _this=this;
			let opts = {
				url: '/base/loadbank/',
				method: 'post'
			};
			let param = {};
			http.httpTokenRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					if (res.data['code'] == 0) {
						uni.hideLoading();
						
						this.bankname=res.data['bankinfo'].bankname;
						this.bankno=res.data['bankinfo'].bankno;
						this.noname=res.data['bankinfo'].noname;
					} else {
						
					}
				},
				error => {
					console.log(error);
				}
			);
		},
		
	},
	onLoad() {
		this._loadbank();
	}

}
